/* Menu styles */
.main-nav {
  list-style: none;
  margin: 0;
  padding: 0;
  position: relative;
  display: flex;
  justify-content: center; /* Center the menu horizontally */
  
}

.main-nav li {
  position: relative;
  flex: 1;
  text-align: center; /* Align menu items in the center */
  list-style: none;
  text-transform: uppercase;
  font-weight: bolder;
  
}

.main-nav li a {
  color: #333;
  text-decoration: none;
  padding: 10px;
  display: block;
  
}

/* Dropdown menu styles */
.dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #080303;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  padding: 10px;
  z-index: 1;
  text-align: center; /* Align the dropdown menu items in the center */
}

.nav-item:hover .dropdown-menu {
  display: block;
}

.dropdown-menu li {
  padding: 5px 0;
}

.dropdown-menu li a {
  color: #fff;
  text-decoration: none;
}

/* Additional styling */
.header {
  padding: 10px;
  background-color: #f0f0f0;
  border-bottom: 1px solid #ddd;
}

/* Submenu styles */
.submenu-label {
  font-weight: bold;
  color: #fff;
  margin-bottom: 10px;
}

.submenu-item {
  color: #ccc;
}

.submenu-item:hover {
  color: #fff;
}

/* Center the dropdown menu */
.dropdown-menu {
  left: 50%;
  transform: translateX(-50%);
}
